import { Button, Form, Input, Select, InputNumber } from "antd";

import styles from "./StartForm.modal.css";

const { Option } = Select;

const layout = {
  labelCol: { span: 7 },
  wrapperCol: { span: 39 },
};

const StartForm = ({ closeModal, setUserInfo }) => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    setUserInfo(values);
    closeModal();
  };

  return (
    <Form
      {...layout}
      form={form}
      name="control-hooks"
      onFinish={onFinish}
      style={{
        maxWidth: 1000,
      }}
    >
      <Form.Item
        name="userName"
        label="Ваше имя"
        rules={[
          {
            required: true,
            message: "Пожалуйста введите свое имя",
          },
        ]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        name="gender"
        label="Пол"
        rules={[
          {
            required: true,
            message: "Пожалуйста укажите свой пол",
          },
        ]}
      >
        <Select placeholder="Укажите ваш пол" allowClear>
          <Option value="male">Мужской</Option>
          <Option value="female">Женский</Option>
        </Select>
      </Form.Item>

      <Form.Item
        name="activityLevel"
        label="Уровень активности"
        rules={[
          {
            required: true,
            message: "Пожалуйста укажите свой уровень активности",
          },
        ]}
      >
        <Select placeholder="Выберите уровень активности" allowClear>
          <Option value="low">Низкий (1-2 часa в день)</Option>
          <Option value="medium">Средний (3-5 часов в день)</Option>
          <Option value="heavy">Высокий (&gt;5 часов в день)</Option>
        </Select>
      </Form.Item>

      <Form.Item
        name="weight"
        label="Масса тела"
        rules={[
          {
            required: true,
            message: "Пожалуйста укажите массу тела",
          },
        ]}
      >
        <InputNumber min={40} max={200} />
      </Form.Item>

      <Form.Item style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          type="default"
          htmlType="button"
          onClick={closeModal}
          style={{ marginRight: 10 }}
        >
          Отмена
        </Button>
        <Button type="primary" htmlType="submit">
          Рассчитать
        </Button>
      </Form.Item>
    </Form>
  );
};
export default StartForm;
