export let data = [
  { name: "Пн", uv: 0, waterRate: 5000 },
  { name: "Вт", uv: 0, waterRate: 5000 },
  { name: "Ср", uv: 0, waterRate: 5000 },
  { name: "Чт", uv: 0, waterRate: 5000 },
  { name: "Пт", uv: 0, waterRate: 5000 },
  { name: "Сб", uv: 0, waterRate: 5000 },
  { name: "Вс", uv: 0, waterRate: 5000 },
];

export const setChartData = (date, value) => {
  const dayNum = date.getDay();
  let day;
  switch (dayNum) {
    case 0:
      day = "Вс";
      break;
    case 1:
      day = "Пн";
      break;
    case 2:
      day = "Вт";
      break;
    case 3:
      day = "Ср";
      break;

    case 4:
      day = "Чт";
      break;
    case 5:
      day = "Пт";
      break;
    case 6:
      day = "Сб";
      break;
    default:
      break;
  }

  data = data.map((item) => {
    if (item.name !== day) return item;

    item.uv = value;
    return item;
  });
};

export const setWaterRate = (count) => {
  data = data.map((item) => {
    item.waterRate = count;
    return item;
  });
};
